/**
 * = Backgrounds
*/

@each $color, $value in $theme-colors {
  @include bg-gradient-variant('.bg-gradient-#{$color}', $value);
}

// Backgrounds Overlay
@each $color, $value in $theme-colors {
  .overlay-#{$color} {
    &:before {
      position: absolute;
      content: '';
      background: $value;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0.8;
      z-index: 0;
    }
  }
}

.section-image {
  @include background-image(no-repeat, cover);
}

.bg-secondary-alt {
  background-color: lighten($secondary, 15%);
}

.form-bg-image {
  background-repeat: no-repeat !important;
  background-position: top center !important;
}

.fill-soft {
  fill: $soft;
}

.bg-gray-800 {
  --bs-bg-opacity: 1;
  background-color: rgba(31, 41, 55, var(--bs-bg-opacity)) !important;
  background-color: rgba(#1f2937, var(--bs-bg-opacity)) !important;
}
